import {LitElement, css, html} from 'lit';
import {customElement, property} from 'lit/decorators.js';

const PLAY_BUTTON = html`<svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 16 16"><path d="m11.596 8.697-6.363 3.692c-.54.313-1.233-.066-1.233-.697V4.308c0-.63.692-1.01 1.233-.696l6.363 3.692a.802.802 0 0 1 0 1.393z"/></svg>`;
const PAUSE_BUTTON = html`<svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 16 16"><path d="M5.5 3.5A1.5 1.5 0 0 1 7 5v6a1.5 1.5 0 0 1-3 0V5a1.5 1.5 0 0 1 1.5-1.5zm5 0A1.5 1.5 0 0 1 12 5v6a1.5 1.5 0 0 1-3 0V5a1.5 1.5 0 0 1 1.5-1.5z"/></svg>`;
const SPOTIFY_BUTTON = html`<svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 16 16"><path d="M8 0a8 8 0 1 0 0 16A8 8 0 0 0 8 0zm3.669 11.538a.498.498 0 0 1-.686.165c-1.879-1.147-4.243-1.407-7.028-.77a.499.499 0 0 1-.222-.973c3.048-.696 5.662-.397 7.77.892a.5.5 0 0 1 .166.686zm.979-2.178a.624.624 0 0 1-.858.205c-2.15-1.321-5.428-1.704-7.972-.932a.625.625 0 0 1-.362-1.194c2.905-.881 6.517-.454 8.986 1.063a.624.624 0 0 1 .206.858zm.084-2.268C10.154 5.56 5.9 5.419 3.438 6.166a.748.748 0 1 1-.434-1.432c2.825-.857 7.523-.692 10.492 1.07a.747.747 0 1 1-.764 1.288z"/></svg>`

@customElement('track-display')
export class TrackDisplay extends LitElement {

  static styles = css`
    .wrapper {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: var(--padding);
    }

    .wrapper > .picture {
      flex: 1 0 var(--art-size);
      width: var(--art-size);
      height: var(--art-size);
      position: relative;
      color: rgba(128,128,128,0.8);
      background-color: #aaa;
    }

    .wrapper > .picture > img {
      width: var(--art-size);
      height: var(--art-size);
    }

    .wrapper > .details {
      flex: 1 1 100%;
    }

    .wrapper > .details > .artist {
      font-weight: bold;
      line-height: 1;
    }

    .wrapper > .details > .track {
      font-size: 140%;
      line-height: 1;
    }

    .play-button {
      color: rgba(192,192,192,0.4);
      mix-blend-mode: hard-light;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      position: absolute;
    }

    .play-button:hover {
      color: rgba(192,192,192,0.9);
    }

    .external-player {
      color: black;
      opacity: 0.4;
      transition: opacity ease-in-out 0.15s;
      display: inline-block;
      width: calc(var(--art-size)/3);
      heigth: calc(var(--art-size)/3);
    }

    .external-player:hover {
      opacity: 1.0;
    }
  `;

  // Declare reactive properties
  @property()
  albumArt: string;

  @property({reflect: true})
  artist: string;

  @property({reflect: true})
  track: string;

  @property()
  playbackUrl: string;

  @property()
  spotifyUrl: string;

  constructor() {
    super();
    this._audio.addEventListener('ended', (evt) => {

      this.requestUpdate();
    });
  }

  updated(changedProperties) {
    if (changedProperties.has('playbackUrl')) {
      const isPlaying = this._audio?.paused === false;

      this._audio.src = this.playbackUrl;
      if (isPlaying) {
        this._audio.play();
      }
    }
    super.updated(changedProperties);
  }

  // Render the UI as a function of component state
  render() {
    let audioButton = html``;
    if (this.playbackUrl) {
      const buttonArt = this._audio.paused ? PLAY_BUTTON : PAUSE_BUTTON;
      audioButton = html`<div class="play-button">${buttonArt}</div>`;
    }

    let spotifyButton = html``;
    if (this.spotifyUrl) {
      spotifyButton = html`<a class="external-player" href="${this.spotifyUrl}" target="_blank">${SPOTIFY_BUTTON}</a>`;
    }

    let albumArt = html``;
    if (this.albumArt) {
      albumArt = html`<img src="${this.albumArt}">`;
    }

    return html`<div class="wrapper">
      <div class="picture" @click="${this._playPauseClick}">
        ${albumArt}
        ${audioButton}
      </div>
      <div class="details">
        <div class="artist">${this.artist}</div>
        <div class="track">${this.track}</div>
      </div>
      <div class="links">
        ${spotifyButton}
      </div>
    </div>`;
  }

  _playPauseClick(e: Event) {
    if (!this._audio.paused) {
      this._audio.pause();
    } else if (this.playbackUrl) {
      this._audio.src = this.playbackUrl;
      this._audio.play();
    }
    this.requestUpdate();
  }

  _openSpotify(e: Event) {
    window.location.href = this.spotifyUrl;
  }

  pause() {
    this._audio.pause();
  }

  private _audio = new Audio();

}

declare global {
  interface HTMLElementTagNameMap {
    "track-display": TrackDisplay,
  }
}
